var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('pre',{staticClass:"annotation_ru none",staticStyle:{"text-align":"left","opacity":"0"}},[_vm._v("Двадцать пятого мая 2019 года место, известное в Краснодаре как \"кладбище барж\", было захвачено и в его рамках были проведены выставка 22 художников и концерт синти-поп музыканта ВАТСКИИ. Событие было названо «Ковчег Культуры», а мифологическим организатором выступил Независимый Филиал Кубанского Речного Пароходства (как оппозиция организации, владеющей этой территорией).\n\n« ... вы сможете пройти извилистый путь к Ковчегу и спастись от гнёта асфальта, заборов и машин. Или погрузиться в ещё больший индустриальный ад ... »\n\nБаржи расположены между двух берегов, захваченных разными силами — с одной стороны это Кубанское Речное Пароходство, территория бизнеса (они добывают здесь песок, а также владеют другими прибрежными территориями, где строят элитное жильё), забравшего себе часть берега и не дающего прохода к нему; с другой стороны государственное учреждение \"Региональный Центр Спортивной подготовки №1\", в котором за всё время мы видели только охранника. Этот центр захватил огромный полуостров, отделив свою территорию сеткой Рабица, оставив узкую полоску берега жителям города. Помимо этого, проход на полуостров тоже ограждён уже профнастиловым забором, но к счастью был оставлен небольшой проход на территорию полуострова. Пройдя два слоя заборов, нужно было идти по узкой прибрежной тропинке по лесу вдоль забора через комаров и мошек по грязи. В конце этого пути, преодолев собранный нами деревянный трап, посетители попадали на борт ржавых, видимо очень редко используемых, посудин, которые связаны между собой канатами. Приглашённые художники могли использовать внутренние трюмы и наружные части барж. Концерт был организован внутри одной из барж, освещение и оборудование питались от небольшого генератора, расположенного на соседней барже.\n\nНаходясь между двух захваченных берегов на ржавой посудине, до которой ещё непросто дойти, баржи являлись прямой иллюстрацией "),_c('a',{attrs:{"href":"http://instructions.stranno.su/#legal"}},[_vm._v("полулегальности")]),_vm._v(" — возникающей пустоты посреди конкуренции сил власти и безнеса, которую мы попытались заполнить. Выражаем благодарность смелым художникам и посетителям, которые, несмотря на дождь, пришли стать соучастниками этого заполнения.\n\nУчастники:\n\nSad Face\nSalien Adare\nАлександр Орлов\nАлла Пасмашная\nАртур Голяков\nВАТСКИИ\nВалера Свидетель\nВаня Венмер\nВладимир Омутов\nГруппа Кюс\nДиана Струтинская\nЕлена Терешкова\nИра Афанасьева\nКатрин Ковалёнок\nМариам Абеляр\nМакс Казимир\nНоооЖ\nОлег Черников\nПаша Безор\nСергей Олейник\nСтас Тоска\nЭльдар Ганеев\nЮлия Шафаростова\n\nДокументация: "),_c('a',{attrs:{"href":"https://vk.com/ark_of_culture"}},[_vm._v("vk")]),_vm._v(" | "),_c('a',{attrs:{"href":"https://www.facebook.com/events/298874601021705/"}},[_vm._v("fb")]),_vm._v(" | "),_c('a',{attrs:{"href":"https://www.yuga.ru/news/441916/"}},[_vm._v("СМИ")]),_vm._v("\n")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('pre',{staticClass:"annotation_en",staticStyle:{"text-align":"left"}},[_vm._v("On the 25th of May 2019 the place known in Krasnodar as the \"cemetery of barges\" was occupied and within its framework the exhibition of 22 artists and a concert of synth-pop musician VATSKYY was held. The event was named \"The Ark of Culture\" and the Independent Branch of the Kuban River Shipping Company was the mythological organizer (as an opposition to the organization that owns this territory).\n\n« ... you will be able to walk a winding path to the Ark and escape from the oppression of asphalt, fences and cars. Or plunge into an even bigger industrial hell ... »\n\nBarges are located between two coasts occupied by different forces — on the one hand it is the Kuban River Shipping Company, a business territory (they extract sand here and also own other coastal areas where elite housing is being built), which has taken a part of the coastline and does not give access to it; on the other hand it is a state institution \"Regional Center for Sports Training №1\", where we have always seen only a guard. This center has grasped a huge peninsula, having separated the territory by grid Rabitsa (chain-link fence), having left a narrow strip of coast to inhabitants of the city. In addition, the passage to the peninsula is also fenced with a corrugated galvanised iron, but fortunately a small passage to the peninsula was left. Having passed two layers of fences, it was necessary to go on a narrow coastal path along a wood along a fence through mosquitoes and gnats on a dirt. At the end of this journey, having overcome the wooden gangway we had assembled, the visitors came aboard rusty, apparently very seldom used, ships that were connected by ropes. Invited artists could use the inside bilges and outside elements of barges. The concert was organized inside one of the barges, and the lighting and equipment were powered by a small generator located on the adjacent barge.\n\nLocated between the two occupied shores on a rusty vessel that was still difficult to reach, the barges were a direct illustration of the "),_c('a',{attrs:{"href":"http://instructions.stranno.su/#legal"}},[_vm._v("semi-legality")]),_vm._v(" - an emerging void in the midst of the competition between the authorities and the lack of carrying power that we tried to fill. We would like to thank the brave artists and visitors who, despite the rain, came to participate in this filling.\n\nParticipants:\n\nSad Face\nSalien Adare\nAlexander Orlov\nAlla Easter\nArthur Golyakov\nVATSKYY\nValera The Witness\nVanya Venmer\nVladimir Omutov\nKiuss Group\nDiana Strutinskaya\nElena Tereshkova\nIra Afanasieva\nCatherine Kovalionok\nMariam Abailard\nMax Casimir\nNOOOZH\nOleg Chernikov\nPasha Bezor\nSergei Oleynyk\nStas Toska\nEldar Ganeyev\nYulia Shafarostova\n\nDocumetation: "),_c('a',{attrs:{"href":"https://vk.com/ark_of_culture"}},[_vm._v("vk")]),_vm._v(" | "),_c('a',{attrs:{"href":"https://www.facebook.com/events/298874601021705/"}},[_vm._v("fb")]),_vm._v(" | "),_c('a',{attrs:{"href":"https://www.yuga.ru/news/441916/"}},[_vm._v("media")]),_vm._v("\n")])}]
